<!--food详情页组件-->
<template>
    <transition name="move">
        <div v-show="showFlag" class="food" ref="food">
            <div class="food-content">
                <div class="image-header">
                    <img :src="food.image" alt="">
                    <div class="back" @click="close">
                        <i class="icon-arrow_lift"></i>
                    </div>
                </div>
                <div class="content">
                    <h1 class="title">{{food.name}}</h1>
                    <div class="detail">
                        <span class="sell-count">月售{{food.sellCount}}</span>
                        <span class="rating">好评率{{food.rating}}%</span>
                    </div>
                    <div class="price">
                        <span class="now">￥{{food.price}}</span>
                        <span class="old" v-show="food.oldProce">￥{{food.oldPrice}}</span>
                        <!--<span class="old" >￥12</span>-->
                    </div>
                    <div class="cartcontrol-wrapper">
                        <cartcontral :food="food"></cartcontral>
                    </div>
                    <transition name="fade">
                        <div @click.stop="addFirst(food,$event)" class="buy" v-show="!food.count || food.count === 0">加入购物车</div>
                    </transition>
                </div>
                <split v-show="food.info"></split>
                <div class="info" v-show="food.info">
                    <h1 class="title">商品信息</h1>
                    <p class="text">{{food.info}}</p>
                </div>
                <split></split>
                <div class="rating">
                    <h1 class="title">商品评价</h1>
                    <ratingselect @select_type="select_type" @only_content="toggleContent" :ratings="food.ratings" :desc="desc" :selecttype="selectType" :onlycontent="onlycontent"></ratingselect>
                    <div class="rating-wrapper">
                        <ul v-if="food.ratings!== undefined && food.ratings!== null && food.ratings.length > 0 ">
                            <li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" :key="rating.index" class="rating-item border-1px">
                                <div class="user">
                                    <span class="name">{{rating.username}}</span>
                                    <img :src="rating.avatar" alt="" class="avatar" width="12px" height="12px">
                                </div>
                                <div class="time">{{rating.rateTime | formatDate}}</div>
                                <p class="text">
                                    <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
                                </p>
                            </li>
                        </ul>
                        <div class="no-rating" v-else>暂无评价</div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
import BScroll from 'better-scroll';
import cartcontral from '../cartcontrol/cartcontrol';
import split from '../split/split';
import ratingselect from '../ratingselect/ratingselect';
import Vue from 'vue';
import {formatDate} from '@/common/js/date';

const ALL = 2;
export default {
  name: 'name',
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    show() {
      this.showFlag = true;
      this.onlycontent = true;
      this.selectType = ALL;
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.food, {
            click: true
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    close() {
      this.showFlag = false;
    },
    addFirst(food, event) {
      if (!event._constructed) {
      } else {
        this.$parent.$refs.shopcart.drop(event.target);
        Vue.set(this.food, 'count', 1);
      }
    },
    needShow(type, text) {
      if (this.onlycontent && !text) {
        return false;
      }
      if (this.selectType === ALL) {
        return true;
      } else {
        if (type === this.selectType) {
          return true;
        } else {
          return false;
        }
      }
    },
    select_type(type) {
      this.selectType = type;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    },
    toggleContent(content) {
      this.onlycontent = content;
      this.$nextTick(() => {
        this.scroll.refresh();
      });
    }
  },
  data() {
    return {
      showFlag: false,
      selectType: ALL,
      onlycontent: true,
      desc: {
        all: '全部',
        positive: '推荐',
        negative: '吐槽'
      }
    };
  },
  components: {
    'cartcontral': cartcontral,
    'split': split,
    'ratingselect': ratingselect
  },
  filters: {
    formatDate (time) {
      let date = new Date(time);
      return formatDate(date, 'yyyy-MM-dd hh:mm');
    }
  }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin.styl";
.food
    position fixed;
    left:0
    top:0
    bottom 48px
    z-index 30
    width 100%
    background: #fff;
    &.move-transition
        transform translate3d(0,0,0)
        transition all 0.2s linear
    &.move-enter-active,&.move-leave-active
        transition all 0.4s
    &.move-enter,&.move-leave-active
        transform translate3d(100%,0,0)
    .food-content
        /*position relative*/
        width 100%
        .image-header
            position relative
            width 100%
            height 0
            padding-top 100%
            img
                position absolute
                top:0
                left:0
                height 100%
                width 100%
            .back
                position absolute
                top:10px
                left 0
                .icon-arrow_lift
                    width 20px
                    height 20px
                    display block
                    padding 10px
                    font-size 20px
                    color: #fff
        .content
            position relative
            padding 18px
            .title
                line-height 14px
                margin-bottom 8px
                font-size: 14px
                font-weight 700
                color: rgb(7,17,27)
            .detail
                margin-bottom 18px
                line-height 10px
                height 10px
                font-size 0
                .sell-count,.rating
                    font-size 10px
                    color: rgb(147,153,159)
                .sell-count
                    margin-right 12px
            .price
                font-weight 700
                line-height 24px
                .now
                    margin-right 8px
                    font-size 14px
                    color rgb(240,20,20)
                .old
                    text-decoration line-through
                    font-size 10px
                    color: rgb(147,153,159)
            .cartcontrol-wrapper
                position absolute
                right 12px
                bottom 12px
            .buy
                position absolute
                bottom 18px
                right 18px
                z-index 10
                line-height 28px
                height 28px
                padding 0 12px
                box-sizing border-box
                border-radius 12px
                color #fff
                background: rgb(0,160,220);
                font-size 10px
                &.fade-enter-active,&.fade-leave-active
                    transition all 0.5s
                &.fade-enter,&.fade-leave-to
                    opacity 0
                &.fade-leave
                    opacity 0
        .info
            padding 18px
            .title
                line-height 14px
                font-size 14px
                color: rgb(7,17,27)
                margin-bottom 6px
            .text
                line-height 24px
                padding 0 8px
                color: rgb(77,85,93)
                font-size 12px
        .rating
            padding-top 18px
            .title
                line-height 14px
                margin-left 18px
                color rgb(7,17,27)
                font-size 14px
            .rating-wrapper
                padding 0 18px
                .rating-item
                    position relative
                    padding 16px 0
                    border-1px(rgba(7,17,27,0.1))
                    .user
                        position absolute
                        top 16px
                        font-size 0
                        right:0
                        .name
                            display inline-block
                            vertical-align top
                            color rgb(147,153,159)
                            margin-right 6px
                            font-size 10px
                        .avatar
                            border-radius 50%
                    .time
                        margin-bottom 6px
                        line-height 16px
                        font-size 10px
                        color rgb(147,153,159)
                    .text
                        line-height 16px
                        color rgb(7,17,27)
                        font-size 12px
                        .icon-thumb_up, .icon-thumb_down
                            margin-right 4px
                            font-size 12px
                        .icon-thumb_up
                            color: rgb(0,160,220)
                        .icon-thumb_down
                            color rgb(7,17,27)
                .no-rating
                    padding 16px 0
                    color rgb(147,153,159)
                    font-size 12px
</style>



