<template>
  <div id="app">
    <v-header :seller="seller"></v-header>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link to="/goods" tag="a">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings" tag="a">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/sellers" tag="a">商家</router-link>
      </div>
    </div>
    <keep-alive><router-view :seller="seller" ></router-view></keep-alive>
  </div>
</template>

<script>
import header from '@/components/header/header';
import axios from 'axios';
export default {
  name: 'App',
  components: {
    'v-header': header
  },
  data () {
    return {
      seller: {}
    };
  },
  created () {
    axios.get('/api/seller').then((response) => {
      let errno = response.data.errno;
      let data = response.data.data;
      if (errno === 0) {
        this.seller = data;
      } else {
      }
    });
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "./common/stylus/mixin.styl"
  #app
    .tab
      display: flex
      width 100%
      height 40px
      line-height 40px
      border-1px(rgba(7, 17, 27, 0.1))
      .tab-item
        flex 1
        text-align center
        & > a
          display block
          font-size 14px
          color rgb(77, 85, 93)
          &.active
            color rgb(240, 20, 20)
</style>



