<template>
    <div class="seller" ref="seller">
        <div class="seller-content">
            <div class="overview">
                <h1 class="title">{{seller.name}}</h1>
                <div class="desc border-1px">
                    <star :size="36" :score="seller.score"></star>
                    <span class="text">({{seller.ratingCount}})</span>
                    <span class="text">月售{{seller.sellCount}}单</span>
                </div>
                <ul class="remark">
                    <li class="block">
                        <h2>起送价</h2>
                        <div class="content">
                            <span class="stress">{{seller.minPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>商家配送</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>平均配送时间</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryTime}}</span>分钟
                        </div>
                    </li>
                </ul>
                <div class="favorite" @click="toggleFavorite($event)">
                    <span class="icon-favorite" :class="{'activate': favorite}"></span>
                    <span class="text">{{favoriteText}}</span>
                </div>
            </div>
            <split></split>
            <div class="bulletin">
                <h1 class="title">公告与活动</h1>
                <div class="content-wrapper border-1px">
                    <p class="content">{{seller.bulletin}}</p>
                </div>
                <ul v-if="seller.supports" class="supports">
                    <li class="support-item border-1px" v-for="item in seller.supports" :key="item.index">
                        <span class="icon" :class="classMap[item.type]"></span>
                        <span class="text">{{item.description}}</span>
                    </li>
                </ul>
            </div>
            <split></split>
            <div class="pics">
                <h1 class="title">商家实景</h1>
                <div class="pic-wrapper" ref="pic">
                    <ul class="pic-list" ref="pic_list">
                        <li class="pic-item" v-for="pic in seller.pics" :key="pic.index">
                            <img :src="pic" alt="" width="120px" height="90px">
                        </li>
                    </ul>
                </div>
            </div>
            <split></split>
            <div class="info">
                <h1 class="title border-1px">商家信息</h1>
                <ul>
                    <li class="info-item" v-for="info in seller.infos" :key="info.index">{{info}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import star from '../star/star';
    import split from '../split/split';
    import BScroll from 'better-scroll';
export default {
  name: 'sellers',
  data() {
    return {
      favorite: false
    };
  },
  computed: {
    favoriteText() {
      if (this.favorite === true) {
        return '已收藏';
      } else {
        return '收藏';
      }
    }
  },
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    'star': star,
    'split': split
  },
  created() {
    this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    this.$nextTick(() => {
      this._initScroll();
      this._initPicScroll();
    });
  },
  methods: {
    _initScroll () {
      if (this.scroll) {
        this.scroll.refresh();
      }
      this.scroll = new BScroll(this.$refs.seller, {
        click: true
      });
    },
    _initPicScroll () {
      if (this.seller.pics) {
        let picWidth = 120;
        let margin = 6;
        let width = (picWidth + margin) * this.seller.pics.length - margin;
        this.$refs.pic_list.style.width = width + 'px';
        if (this.picScroll) {
          this.picScroll.refresh();
        }
        this.picScroll = new BScroll(this.$refs.pic, {
          scrollX: true,
          eventPassthrough: 'vertical'
        });
      }
    },
    toggleFavorite(event) {
      if (event._constructed) {
        this.favorite = !this.favorite;
      }
    }
  }
  // watch: {
  //   'seller'(oldValue, newValue) {
  //     console.log(oldValue, newValue);
  //   }
  // }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin.styl";
.seller
    position absolute
    top 174px
    bottom 0
    left:0
    width 100%
    overflow hidden
    .overview
        padding 18px
        position: relative
        .title
            margin-bottom 8px
            line-height 14px
            color rgb(7,17,27)
            font-size 14px
        .desc
            padding-bottom 18px
            font-size 0
            border-1px(rgba(7,17,27,0.1))
            .star
                display inline-block
                margin-right 8px
                vertical-align top
            .text
                margin-right 12px
                vertical-align top
                display inline-block
                line-height 18px
                font-size 10px
                color rgb(147,153,159)
        .remark
            display flex
            padding-top 18px
            .block
                flex 1
                text-align center
                border-right 1px solid rgba(7,17,27,0.2)
                &:last-child
                    border-right 0
                h2
                    margin-bottom 4px
                    line-height 10px
                    font-size 10px
                    color rgb(147,153,159)
                .content
                    line-height 24px
                    font-size 10px
                    color: rgb(7,17,27)
                .stress
                    font-size 24px
        .favorite
            position absolute
            width 50px
            right 18px
            top:18px
            text-align center
            .icon-favorite
                display block
                color #d4d6d9
                line-height 24px
                font-size 24px
                &.activate
                    color rgb(240,20,20)
            .text
                line-height 10px
                font-size 10px
                color: rgb(77,80,93)
    .bulletin
        padding 18px 18px 0 18px
        .title
            margin-bottom 8px
            line-height 14px
            color rgb(7,17,27)
            font-size 14px
        .content-wrapper
            padding 0 12px 16px 12px
            border-1px(rgba(7,17,27,0.2))
            .content
                line-height 24px
                font-size 12px
                color: rgb(240,20,20)
        .supports
            .support-item
                padding 16px 12px
                font-size 0
                border-1px(rgba(7,17,27,0.2))
                &:last-child
                    border-none()
                .icon
                    display inline-block
                    width 16px
                    height 16px
                    vertical-align: top
                    margin-right 6px
                    background-size 16px 16px
                    background-repeat no-repeat
                    &.decrease
                        bg-image('decrease_4')
                    &.discount
                        bg-image('discount_4')
                    &.guarantee
                        bg-image('guarantee_4')
                    &.invoice
                        bg-image('invoice_4')
                    &.special
                        bg-image('special_4')
                .text
                    font-size 12px
                    line-height 16px
                    color rgb(7,17,27)
    .pics
        padding 18px
        .title
            margin-bottom 12px
            line-height 14px
            color: rgb(7,17,27)
            font-size 14px
        .pic-wrapper
            width 100%
            overflow hidden
            white-space nowrap
            .pic-list
                font-size 0
                .pic-item
                    display inline-block
                    margin-right 6px
                    width 120px
                    height 90px
                    &:last-child
                        margin 0
    .info
        padding 18px 18px 0 18px
        .title
            padding-bottom 12px
            line-height 14px
            border-1px(rgba(7,17,27,0.2))
            color rgb(7,17,27)
            font-size 14px
        .info-item
            padding 16px 12px
            border-1px(rgba(7,17,27,0.2))
            color rgb(7,17,27)
            line-height 16px
            font-size 12px
            &:last-child
                border-none()
</style>



